<template>
  <div id="app">
    {{ saveUsersTheme() }}
    <app-header/>
    <div class="contentArea">
      <router-view/>
    </div>
    <app-footer/>
  </div>
</template>

<script>
import Header from "@/components/Header"
import Footer from "@/components/footerArea"

const userModeInfo = window.matchMedia('(prefers-color-scheme: Dark)').matches;

export default {
  components: {
    'app-header': Header,
    'app-footer': Footer
  },
  methods: {
    saveUsersTheme: function () {

      // Dark Mode  : userModeInfo == true
      // Light Mode : userModeInfo == false
      if (userModeInfo == true) {

        // Dark Mode
        let userMode = 'dark';
        localStorage.setItem("userMode", userMode);

      } else {

        // Light Mode
        let userMode = 'light';
        localStorage.setItem("userMode", userMode);

      }
    }
  },
  mounted() {

    const selectorObject = document.getElementById('themeSelector');

    // ÏòµÏÖò ÌÉúÍ∑∏Îì§Ïùò selected ÏÜçÏÑ± Ï†ÑÎ∂Ä Ï¥àÍ∏∞Ìôî
    selectorObject[0].removeAttribute('selected');
    selectorObject[1].removeAttribute('selected');
    selectorObject[2].removeAttribute('selected');

    // localstorage Ïóê ÏûàÎäî Í∞íÏùÑ Í∏∞Ï§ÄÏúºÎ°ú ÌÉúÍ∑∏Îì§Ïóê selecred ÏÜçÏÑ± Ï∂îÍ∞Ä.
    switch (localStorage.getItem('setMode')) {
      case 'light' :
        selectorObject[1].setAttribute('selected', '');
        break;
      case 'dark'  :
        selectorObject[2].setAttribute('selected', '');
        break;
    }

    const bodyObject = document.getElementsByTagName('body')[0];

    if (localStorage.getItem('setMode') !== null) {
      // Ï≤òÏùå Î°úÎìúÏãú Ïú†Ï†ÄÍ∞Ä ÌÖåÎßàÎ•º ÏÑ∏ÌåÖÌï¥ÎÜìÏùÄ Ï†ïÎ≥¥Í∞Ä ÏûàÏùÑ Îïå. ÎèôÏûë ÏïàÌï®.
      if (localStorage.getItem('setMode') == 'dark') {
        bodyObject.className = "darkMode";
      } else {
        bodyObject.className = "";
        bodyObject.removeAttribute('class');
      }

    } else {
      // Ï≤òÏùå Î°úÎìúÏãú setMode Îç∞Ïù¥ÌÑ∞ ÎπÑÏñ¥ÏûàÏùÑ Îïå.
      if (localStorage.getItem('userMode') == 'dark') {
        bodyObject.className = "darkMode";
      } else {
        bodyObject.className = "";
        bodyObject.removeAttribute('class');
      }
    }
  }

}

</script>
